import {
    Mesh,
    Vector3,
    Group,
    MeshBasicMaterial,
    PlaneGeometry,
} from "three";

import Common from "@/graphics/Common";

import gsap from '@gsap/shockingly';
import Input from "@/graphics/Input";
// import WebGPU from 'three/addons/capabilities/WebGPU.js';
// import WebGL from 'three/addons/capabilities/WebGL.js';
// console.log("WebGPU", WebGPU.isAvailable());
// console.log("WebGL", WebGL.isWebGL2Available());
function lonLatToVector3( lng, lat, out )
{
    out = out || new Vector3();
    lat = Math.PI / 2 - lat;
    out.set(
        Math.sin( lat ) * Math.sin( lng ),
        Math.cos( lat ),
        Math.sin( lat ) * Math.cos( lng )
    );
    return out;
}
  
  export default class Marker extends Group {
    interactives = [];
    constructor({ position }) {
        super();
        const size = 0.025;
        const geometry = new PlaneGeometry( size, size );
        const material = new MeshBasicMaterial({
            color: 0xffffff,
            opacity: 0.0,
            transparent: true
        });
        this.setDecoration(size);
        this.plane = new Mesh( geometry, material );
        this.plane.rotation.z = Math.PI * 0.25;
        const proj = lonLatToVector3(...position, this.position);
        proj.multiplyScalar(1.0125);

        this.add(this.plane);
        this.interactives.push(this.plane);
        // this.boundPointermove = this.handlePointermove.bind(this);
        // document.addEventListener("pointermove", this.boundPointermove, false);
        // console.log(this);
    }
    setDecoration(size) {
        const s = size;
        const geometry = new PlaneGeometry( s, s );
        const material = new MeshBasicMaterial({
            color: 0xffffff,
            wireframe: true,
            transparent: true,
            opacity: 0
        });
        this.decocations = [
            new Mesh(geometry, material),
            new Mesh(geometry, material)
        ]
        this.add(...this.decocations);
    }
    enter() {
        const lerp = {
            x: 0,
            y: 0.5
        };
        const tl = new gsap.timeline();
        tl.to(lerp, {
            x: 1,
            ease: "linear",
            duration: 0.5,
            onUpdate: () => {
                const { x } = lerp;
                this.plane.material.opacity = x;
                this.decocations.forEach(_ => {
                    _.material.opacity = x;
                })
            }
        }, 'a')
        tl.to(lerp, {
            y: 1,
            duration: 0.5,
            onUpdate: () => {
                const { y } = lerp;
                const s = y;
                this.plane.scale.set(s, s, s);
            }
        }, 'a')
        return tl;
    }
    handleRaycast(output) {
        // output;
        Common.raycaster.setFromCamera(Input.coords, Common.camera);
        const intersects = Common.raycaster.intersectObjects(this.interactives, false);

        if (intersects.length > 0) {
            // console.log(output);
            // output.components.postprocessing.mainPass.material.uniforms.stable .value = 0.9;
            // output.components.postprocessing.mainPass.material.uniforms.stable .value = 0;
            output.components.postprocessing.PARAMS.stableAdd = -1.0;
            // this.plane.material.opacity = 0;
            // const intersect = intersects[0];
            // console.log(intersect);
            // const { faceIndex, object } = intersect;
            // const { geometry } = object;
            // const { idx } = geometry.attributes;
            // const id = idx.array[geometry.index.array[faceIndex * 3]];
            // const { userData } =  this.active.data[id];
            // this.center(userData.bounds);
            // activeShape.value = id;
            document.body.style.cursor = 'pointer';
            
        } else {
            output.components.postprocessing.PARAMS.stableAdd = 0.0;
            // output.components.postprocessing.mainPass.material.uniforms.stable .value = 1.0;
            // activeShape.value = id;
            // this.plane.material.opacity = 1;

            document.body.style.cursor = "unset";
        }
    }
    render() {
        this.lookAt( Common.camera.position );
        this.decocations[0].rotation.z += 0.01
        this.decocations[1].rotation.z -= 0.01
    }
    dispose() {
        // document.removeEventListener("pointermove", this.boundPointermove);
        this.remove(this.plane);
        this.plane.material.dispose();
        this.plane.geometry.dispose();
    }
    resize() {
    }
  }
  