import Device from "@/pure/Device";
import {
    Group,
    PlaneGeometry,
    ShaderMaterial,
    Mesh,
    Color,
    MeshBasicMaterial,
    AmbientLight,
    DirectionalLight,} from "three";

// import { Glyph } from 'three-glyph';

import Assets from "@/graphics/Assets";
// import Common from "@/graphics/Common";

import Panel from "@/graphics/components/Panel/index.js";

import Character from "./Character";
import vertexShader from "./vertex.glsl";
import fragmentShader from "./fragment.glsl";

export default class CharacterMenu extends Group {
    constructor() {
        super();
        this.setOrigin();
        this.resize();
    }
    setOrigin() {
        this.origin = new Group();

        this.geometry = new PlaneGeometry(2, 2, 1);
        this.material = new ShaderMaterial({
        //   glslVersion: GLSL3,
          transparent: true,
          uniforms: {
            tDiffuse: {
                value: Assets.worldMap2,
            },
            tClouds: {
                value: Assets.worldMapClouds,
            },
            atmosphereColor: {
                value: new Color(0xaddfff),
            },
            time: {
                value: 0.
            }
          },
          vertexShader,
          fragmentShader,
        });
        this.mesh = new Mesh(this.geometry, this.material);
        this.origin.add(this.mesh);

        const date = new Date();
        this.panel = new Panel({
            title: "EARTH  MILKY WAY",
            count: {
                label: "SURVEYED",
                type: "%",
                value: 0.0137,
            },
            details: {
                label: `${date.getUTCHours().toString().padStart(2, "0")}:${date.getUTCMinutes().toString().padStart(2, "0")} UT`,
                alignRight: true 
            }
        });
        this.origin.add(this.panel);
        this.panel2 = new Panel({
            title: "ASTRODYNAMICS",
            count: {
                label: "CERTIFICATION",
                type: "%",
                value: 0.13,
            },
            details: {
                label: `SKILL POINTS`,
                count: {
                    value: 9,
                }
            }
        });
        this.add(this.panel2);

        const geometry = new PlaneGeometry(2, 2, 1);
        const material = new MeshBasicMaterial({
            map: Assets.astodynamics,
            transparent: true
        });
        this.badge = new Mesh(geometry, material);
        this.panel2.add(this.badge);

        this.panel3 = new Panel({
            title: "SCAVENGING",
            count: {
                label: "CERTIFICATION",
                type: "%",
                value: 1,
            },
            details: {
                label: `SKILL POINTS`,
                count: {
                    value: 1,
                }
            }
        });
        this.add(this.panel3);


        // this.panel4 = new Panel({
        //     title: "BIKE  ELOPS ",
        //     count: {
        //         label: "HULL",
        //         type: "%",
        //         value: 1,
        //     }
        // });
        // this.add(this.panel4);

        this.badge = new Mesh(geometry, material.clone());
        this.badge.material.map = Assets.scavenging;
        this.panel3.add(this.badge);
        
        this.add(this.origin);

        this.ambientLight = new AmbientLight( 0x0000ff, 1 );
        this.add( this.ambientLight );

        this.directionalLight = new DirectionalLight( 0xff00ff, 5);
        this.add( this.directionalLight );

        this.character = new Character(Assets.dummy);
        this.add(this.character);
    }
    render(t) {
        this.material.uniforms.time.value = t * 0.1;
        this.character.render();
    }
    resize() {
        const y = 1.5;
        this.position.x = Device.fov.width + Device.fov.height * 0.75 + 9;

        this.origin.position.x = -Device.fov.width * 0.5 + 1 + 0.5;
        this.origin.position.y = Device.fov.height * 0.5 - y - 0.5 - 0.25;

        this.panel2.position.x = Device.fov.width * 0.5 - 1 - 0.5;
        this.panel2.position.y = Device.fov.height * 0.5 - y - 0.5 - 0.25;

        this.panel3.position.x = Device.fov.width * 0.5 - 1 - 0.5;
        this.panel3.position.y = -Device.fov.height * 0.5 + y + 1.5;

        // this.panel4.position.x = -Device.fov.width * 0.5 + 1 + 0.5;
        // this.panel4.position.y = -Device.fov.height * 0.5 + y + 1.5;

        this.character.resize();
    }
    dispose() {
        this.remove(this.ambientLight);
        this.remove( this.directionalLight );
        this.remove(this.mesh);
        this.geometry.dispose();
        this.material.dispose();
        // this.glyph3.dispose();
        this.glyph4.dispose();
        this.remove(this.character);
        this.character.dispose();
    }
    setDebug(pane) {
        this.character.setDebug(pane);
    }
}