import {
    Group,
    PlaneGeometry,
    MeshBasicMaterial,
    Mesh,
    Color,
} from "three";

import { Glyph } from 'three-glyph';
import Assets from "@/graphics/Assets";

const material = new MeshBasicMaterial({
    color: 0xece9e3,
    transparent: true,
    opacity: 0.3
});

export default class LineText extends Group {
    PARAMS = {
        fontScale: 0.009,
        lineWidth: 0.01,
    };
    constructor({
        label,
        lineWidth,
        count,
        alignRight
    }) {
        super();
        if (lineWidth) {
            this.PARAMS.lineWidth = lineWidth;
        }

        this.geometry = new PlaneGeometry(2, this.PARAMS.lineWidth, 1);
        this.material = material.clone();

        this.setLayer({
            label,
            alignRight
        });
        if (count) {
            let label = '';
            if (count.type === '%') {
                label += count.value * 100 + '%'
            } else {
                label += count.value;
            }
            this.setLayer({
                label,
                alignRight: true,
                count
            });
        }
        this.resize();
    }
    setLayer({
        label,
        alignRight,
        count
    }) {
        const { fontScale, lineWidth } = this.PARAMS;

        this.label = new Glyph({
            text: label,
            font: Assets.PPNeueMontrealMedium,
            material: Assets.glyphMaterial
        });
        // this.label.material.uniforms.map.value = Assets.map
        this.label.matrixAutoUpdate = false;
        this.label.mesh.matrixAutoUpdate = false;
        this.label.scale.set(fontScale, fontScale, fontScale);
        this.label.alignTop();
        if (alignRight) {
            this.label.alignRight();
            this.label.position.x = 1;
        } else {
            this.label.alignLeft();
            this.label.position.x = -1;
        }
        this.label.position.y = 0.2;
        this.label.updateMatrix();
        this.label.mesh.updateMatrix();
        this.add(this.label);
        
        this.line = new Mesh(this.geometry, this.material);
        this.line.position.y = -(lineWidth - 0.01) * 0.5;
        if (count && count.type) {
            this.line.material = this.material.clone();
            this.line.material.color = new Color(0xece9e3);
            this.line.material.opacity = 1;
            this.line.scale.x = count.value;
            this.line.position.x = -(1 - count.value);
        }
        this.add(this.line);
    }
    render() {
    }
    resize() {
    }
    dispose() {
        while (this.children.length)
        {
            this.remove(this.children[0]);
        }
        this.label.dispose();
        this.geometry.dispose();
        this.material.dispose();
        this.line.dispose();
    }
}