import {
  IcosahedronGeometry,
  Mesh,
  MeshStandardMaterial,
  Color,
  CanvasTexture,
  RepeatWrapping,
  Vector2,
  Vector3,
  UVMapping,
  Group,
  // LinearSRGBColorSpace,
  WebGLRenderTarget,
  LinearFilter,
  HalfFloatType,
} from "three";

import { Glyph } from 'three-glyph';
import gsap from '@gsap/shockingly';
import { easing } from "maath";

import Device from "@/pure/Device";
import Input from "@/graphics/Input";
import Common from "@/graphics/Common";

import Assets from "@/graphics/Assets";

import { FlakesTexture } from 'three/addons/textures/FlakesTexture.js';

import AlphaFragment from "./alpha.fragment.glsl";
import position_pars_vertex from "./position_pars.vertex.glsl";
import color_pars_fragment from "./color_pars.fragment.glsl";
import color_fragment from "./color.fragment.glsl";
import transformed_vertex from "./transformed.vertex.glsl";

import Marker from "@/graphics/components/Atlas/Marker";

export default class Atlas extends Group {
  variables = {
    rotationY: 1.75,
  };
  constructor() {
    super();

    this.target = new WebGLRenderTarget(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio, {
      minFilter: LinearFilter,
      magFilter: LinearFilter,
      type: HalfFloatType,
    })
  }
  init() {
    this.geometry = new IcosahedronGeometry(1, 64);
    const { worldMap, alphaMap, map } = Assets;

    this.material = new MeshStandardMaterial({
      color: new Color(0xdaa520),
      metalness: 1.0,
      roughness: 0.15,
      roughnessMap: worldMap,
      normalMap: null,
      normalScale: new Vector2(0.1, 0.1),
      envMap: Assets.textureCube,
      map: worldMap,
      alphaMap,
      transparent: true,
      userData: {
        transition: { value: 1.0 }
      },
    });

    this.material.normalMap = new CanvasTexture(new FlakesTexture(), UVMapping, RepeatWrapping, RepeatWrapping);
    this.material.normalMap.repeat.set(40, 40);


    this.material.onBeforeCompile = shader => {
      shader.uniforms.transition = this.material.userData.transition
      shader.fragmentShader = 'uniform float transition;\n' + shader.fragmentShader;
      shader.fragmentShader = 
        shader.fragmentShader.replace(
          'vec3 totalSpecular = reflectedLight.directSpecular + reflectedLight.indirectSpecular;',
          'vec3 totalSpecular = reflectedLight.indirectSpecular;'
        )
      shader.fragmentShader = 
        shader.fragmentShader.replace(
          '#include <alphamap_fragment>',
          AlphaFragment
        )
    };

    this.sphere = new Mesh( this.geometry, this.material );
    this.sphere.layers.set(1);
    this.sphere.matrixAutoUpdate = false;
    this.sphere.rotation.y = this.variables.rotationY;
    this.sphere.rotation.x = .875;
    const s = 2.7;
    // this.sphere.scale.set(.9 * 3.0, .9 * 3.0, .9 * 3.0);
    // this.sphere.scale.set(.5 * 3.0, .5 * 3.0, .5 * 3.0);
    this.sphere.scale.set(s, s, s);
    this.sphere.updateMatrix();
    this.add( this.sphere );

    const addons = {
      progress: true,
      shaderChunks: {
        position_pars_vertex,
        transformed_vertex,
        color_pars_fragment,
        color_fragment,
        alpha_fragment:  `alpha *= vProgress * opacity;`,
      }
    }
    this.glyph = new Glyph({
      text: "RMI TRAN",
      font: Assets.LoveFont,
      map,
      color: new Color(0x030c1e),
      letterSpacing: 10,
      addons
    });
    
    this.glyph.matrixAutoUpdate = false;
    this.glyph.mesh.matrixAutoUpdate = false;
    this.glyph.scale.set(0.005, 0.005, 0.005)
    this.glyph.center();
    this.glyph.children[0].geometry.computeVertexNormals();
    this.glyph.position.z = 2.75;

    this.glyph.material.uniforms.progress.value = 0;
    this.glyph.material.uniforms.uTexture = {
      value: null
    };
    this.glyph.material.uniforms.resolution = {
      value: new Vector2(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio)
    };
    this.glyph.material.uniforms.uLight = {
      value: new Vector3(-0.5, 4.0, 10.0),
    };
    this.glyph.material.uniforms.uDiffuseness = {
      value: 0.2,
    };
    this.glyph.material.uniforms.uShininess = {
      value: 40,
    };

    this.glyph.material.uniforms.uIorR = { value: 1.15 };
    this.glyph.material.uniforms.uIorY = { value: 1.16 };
    this.glyph.material.uniforms.uIorG = { value: 1.18 };
    this.glyph.material.uniforms.uIorC = { value: 1.22 };
    this.glyph.material.uniforms.uIorB = { value: 1.22 };
    this.glyph.material.uniforms.uIorP = { value: 1.22 };

    this.glyph.material.uniforms.uSaturation = { value: 1.14 };
    this.glyph.material.uniforms.uChromaticAberration = { value: 0.5 };
    this.glyph.material.uniforms.uRefractPower = { value: 0.25 };

    this.glyph.updateMatrix();
    this.glyph.mesh.updateMatrix();
    // this.add(this.glyph);

    this.glyph2 = new Glyph({
      text: "CODER",
      font: Assets.PPNeueMontrealMedium,
      letterSpacing: 20,
      material: this.glyph.material
    });

    this.glyph2.matrixAutoUpdate = false;
    this.glyph2.mesh.matrixAutoUpdate = false;
    this.glyph2.scale.set(0.005, 0.005, 0.005)
    this.glyph2.position.z = 2.8;
    this.glyph2.position.y = -1.025;
    this.glyph2.center();
    this.glyph2.children[0].geometry.computeVertexNormals();
    this.glyph2.updateMatrix();
    this.glyph2.mesh.updateMatrix();
    // this.add(this.glyph2);
    this.createMarker();
  }
  createMarker() {
    this.marker = new Marker({
      position: [-48.826667, 2.31333]
    });
    this.sphere.add(this.marker);
  }
  enter() {

    const lerp = {
      x: 0,
      y: 0,
      z: 1
    }
    const tl = new gsap.timeline();
    tl.to(lerp, {
      z: 0,
      // repeat: -1,
      duration: 2.5,
      yoyo: true,
      ease: "linear",
      onUpdate: () => {
        this.material.userData.transition.value = lerp.z;
      }
    }, 'a')
    tl.to(lerp, {
      x: 1,
      duration: 1.5,
      ease: "linear",
      onUpdate: () => {
        this.glyph.material.uniforms.progress.value = lerp.x;
      }
    },'a+=1.5')
    tl.to(lerp, {
      y: 1,
      duration: 1,
      ease: "linear",
      onUpdate: () => {
        this.glyph2.material.uniforms.progress.value = lerp.y;
      }
    },'a+=2')
    tl.add(this.marker.enter(), 'a+=2.2')
  }
  handleRaycast(output) {
    this.marker?.handleRaycast(output);
  }
  render(t, delta) {

    easing.damp3(
      this.position,
      [
        this.position.x,
        this.position.y,
        (1.0 - Math.cos(Input.coords.x)) * 2.25
      ],
      0.2,
      delta / 1000,
    )

    easing.damp3(
      this.rotation,
      [
        Input.coords.y * 3.14 * 0.0314 * 0.9,
        Input.coords.x * 3.14 * 0.0314 * 1.4,
        0,
      ],
      0.2,
      delta / 1000,
    );

    if (this.material) {
      easing.damp3(
        this.material.envMapRotation,
        [
          Input.coords.y * 3.14 * 0.0314 * 0.9,
          Input.coords.x * 3.14 * 0.0314 * 1.4,
          0,
          0
        ],
        0.2,
        delta / 1000,
      );
    }

    if (this.glyph && -Device.fov.width < this.parent.position.x) {
      // Common.camera.layers.disableAll();
      // Common.camera.layers.enable(1);
      // // this.glyph.visible = false;
      // // this.glyph2.visible = false;

      // Common.renderer.setRenderTarget(this.target);
      // Common.render();
      
      // this.target.texture.colorSpace = LinearSRGBColorSpace;
      // this.glyph.children[0].material.uniforms.uTexture.value = this.target.texture;
      // this.glyph2.children[0].material.uniforms.uTexture.value = this.target.texture;
      
      // // this.glyph.visible = true;
      // // this.glyph2.visible = true;

      Common.camera.layers.enableAll();

      // this.marker.lookAt( Common.camera.position );
      this.marker.render();
    }

  }
  dispose() {
    this.remove(this.sphere);
    this.remove(this.glyph);
    this.remove(this.glyph2);
    this.sphere.geometry.dispose();
    this.sphere.material.dispose();
    this.glyph.geometry.dispose();
    this.glyph.material.dispose();
    this.glyph2.geometry.dispose();
    this.target.dispose();
    this.marker?.dispose();
  }
  resize() {
    this.glyph?.material.uniforms.resolution.value.set(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio);
    this.target.setSize(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio);
    this.marker?.resize();
  }
}
