import { Howl } from "howler";
import audioFile from '@/assets/sounds/ambient.mp3'
import { spritemap } from '@/assets/sounds/ambient.json'

const sprite = {};
Object.keys(spritemap).forEach(_ => {
    const s = spritemap[_];
    sprite[_] = [s.start * 1000, s.end * 1000, s.loop];
});

var sound = new Howl({
  volume: 0.5,
  src: [audioFile],
  sprite
});

export default sound;