import {
  Mesh,
  PlaneGeometry,
  ShaderMaterial,
  // GLSL3,
  Vector3,
  Group,
  Color,
} from "three";

import Device from "@/pure/Device";
import Assets from "@/graphics/Assets";
import vertexShader from "./vertex.glsl";
import fragmentShader from "./fragment.glsl";

import { Glyph } from 'three-glyph';

export default class MarquePage extends Group {
  constructor() {
    super();
    const material = new ShaderMaterial({
      // glslVersion: GLSL3,
      uniforms: {
        tDiffuse: {
          value: Assets.ladyMargot,
        },
        diffuseAspect: {
          value: 2250 / 1500
        },
        resolution: {
          value: new Vector3(Device.fov.height * 0.75, Device.fov.height)
        },
        ponder: {
          value: 0
        },
      },
      vertexShader,
      fragmentShader,
    });
    const geometry = new PlaneGeometry(1, 1, 1);
    
    const mesh = new Mesh(geometry, material);
    this.add(mesh);

    this.setLegend();

    this.resize();
  }
  setLegend() {
    this.glyph2 = new Glyph({
      text: "COPYRIGHT 2023 WARNER BROS. ENTERTAINMENT INC. ALL RIGHTS RESERVED",
      font: Assets.PPNeueMontrealMedium,
      map: Assets.map,
      color: new Color(0xece9e3),
      // letterSpacing: 20,
    });

    this.glyph2.matrixAutoUpdate = false;
    this.glyph2.mesh.matrixAutoUpdate = false;
    this.glyph2.scale.set(0.00625, 0.00625, 0.00625)
    this.glyph2.rotation.z = -Math.PI * 0.5;
    this.glyph2.center();
    // this.glyph2.updateMatrix();
    this.glyph2.mesh.updateMatrix();
    // this.add(this.glyph2);

  }
  render() {
    const offset = Math.min(1.0, Math.abs(this.parent.position.x) / (Device.fov.width + this.children[0].scale.x), 1.0);
    this.children[0].material.uniforms.ponder.value = 1.0 - offset;
  }
  dispose() {
  }
  resize() {
    const width = Device.fov.height * 0.75;
    this.position.x = Device.fov.width * 0.5 + width * 0.5;
    this.children[0].scale.x = width;
    this.children[0].scale.y = Device.fov.height;

    this.glyph2.position.x = width * 0.5 + 0.15;
    this.glyph2.updateMatrix();
  }
}
