import {
    Group,
    // MeshBasicMaterial,
    ShaderMaterial,
    BackSide,
    // GLSL3,
    Vector2,
} from 'three';
import Assets from "@/graphics/Assets";

import vertexShader from "./vertex.glsl";
import fragmentShader from "./fragment.glsl";
import Device from '@/pure/Device';
import gsap from '@gsap/shockingly';

export default class Gallica extends Group {
    PARAMS = {
      uCover: 0.0,
      transition: 1.0,
    }
    constructor() {
        super();
        this.material = new ShaderMaterial({
          // glslVersion: GLSL3,
          side: BackSide,
          transparent: true,
          uniforms: {
            map: {
              value: null,
            },
            alphaMap: {
              value: Assets.alphaMap,
            },
            opacity: {
              value: 0.3,
            },
            transition: {
              value: this.PARAMS.transition,
            },
            uCover: {
              value: this.PARAMS.uCover,
            },
            resolution: {
              value: new Vector2(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio)
            }
          },
          vertexShader,
          fragmentShader,
        });
        this.globe = Assets.jocodusGlobe.children[0];
        this.globe.children.forEach((_, i) => {
          this.globe.children[i].material = this.material.clone();
          this.globe.children[i].material.uniforms.map.value = Assets.jocodusTextures[i];
        });
        this.globe.scale.set(-50, 50, 50);
        // Assets.jocodusGlobe.traverse(_ => {
        //   if (_.isMesh) _.layers.set(1);
        // })
        this.add(Assets.jocodusGlobe);
    }
    enter() {
      const lerp = {
        x: 1,
      }
      const tl = new gsap.timeline();
      tl.to(lerp, {
        x: 0,
        duration: 1.5,
        ease: "linear",
        onUpdate: () => {
          this.globe.children.forEach(mesh => {
            mesh.material.uniforms.transition.value = lerp.x;
          });
        }
      },'a+=1.5')
    }
    render() {
      const offset = Math.min(1.0, Math.abs(this.parent.parent.position.x) / (Device.fov.width), 1.0);
      this.globe.children.forEach(mesh => {
        mesh.material.uniforms.uCover.value = offset;
      });
    }
    dispose() {
    }
    resize() {
      this.globe.children.forEach(mesh => {
        mesh.material.uniforms.resolution.value.set(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio);
      });

    }
    setDebug(pane) {
        const { PARAMS } = this;

        const folder = pane.addFolder({
          title: 'Gallica',
          expanded: false
        });
        folder
            .addBinding(
            PARAMS,
            'transition',
            {
                label: 'transition',
                step: 0.01,
                min: 0,
                max: 1,
            }
            )
            .on('change', () => {
                this.globe.children.forEach(mesh => {
                  mesh.material.uniforms.transition.value = PARAMS.transition;
                });
            });
    }
  }
  