import {
    Group,
} from 'three';
import { Glyph } from 'three-glyph';

import Device from "@/pure/Device";
import Assets from "@/graphics/Assets";

export default class About extends Group {
    PARAMS = {
    }
    constructor() {
      super();
      const scale = 0.00825;
      this.glyph = new Glyph({
        text: "ABOUT",
        font: Assets.LoveFont,
        material: Assets.glyphMaterial
      });
      this.glyph.matrixAutoUpdate = false;
      this.glyph.mesh.matrixAutoUpdate = false;
      this.glyph.scale.set(scale, scale, scale);
      this.glyph.alignLeft();
      this.glyph.alignBottom();
      this.glyph.position.y = 1.5;
      this.glyph.updateMatrix();
      this.glyph.mesh.updateMatrix();
      this.add(this.glyph);

      this.glyph2 = new Glyph({
        text: "ME",
        font: Assets.LoveFont,
        material: Assets.glyphMaterial
      });
      this.glyph2.matrixAutoUpdate = false;
      this.glyph2.mesh.matrixAutoUpdate = false;
      this.glyph2.scale.set(scale, scale, scale);
      this.glyph2.alignLeft();
      this.glyph2.alignTop();
      this.glyph2.position.y = -1.5;
      this.glyph2.updateMatrix();
      this.glyph2.mesh.updateMatrix();
      this.add(this.glyph2);

      this.glyph3 = new Glyph({
        text: "As a coder deeply rooted in the wisdom of\nantique philosophy and the foundational\nprinciples of ancient science, my approach\nto coding is not merely about writing lines\nof code but about crafting solutions that\nresonate with the timeless truths discovered\nby the great thinkers of the past. I believe\nthat the logic and problem-solving skills\nhoned by these pioneers are as relevant\ntoday as they were centuries ago.",
        font: Assets.PPNeueMontrealMedium,
        material: Assets.glyphMaterial
      });
      this.glyph3.matrixAutoUpdate = false;
      this.glyph3.mesh.matrixAutoUpdate = false;
      this.glyph3.scale.set(scale, scale, scale);
      this.glyph3.center();
      this.glyph3.alignLeft();
      // this.glyph3.position.y = -1.5;
      this.glyph3.updateMatrix();
      this.glyph3.mesh.updateMatrix();
      this.add(this.glyph3);
      
      this.glyph4 = new Glyph({
        text: "For me, coding is an art forma way to\nexpress and embody the harmony, balance,\nand rationality championed by the ancients.\nI approach challenges with a holistic\nmindset, ensuring that my solutions are both\ninnovative and deeply rooted in enduring\nprinciples. My coding journey is guided by\nthe timeless quest for knowledge and\nunderstanding that has propelled humanity\nforward through the ages.",
        font: Assets.PPNeueMontrealMedium,
        material: Assets.glyphMaterial
      });
      this.glyph4.matrixAutoUpdate = false;
      this.glyph4.mesh.matrixAutoUpdate = false;
      this.glyph4.scale.set(scale, scale, scale);
      this.glyph4.center();
      this.glyph4.alignLeft();
      this.glyph4.position.x = 3;
      this.glyph4.updateMatrix();
      this.glyph4.mesh.updateMatrix();
      this.add(this.glyph4);

      this.resize();
    }
    render() {
    }
    dispose() {
    }
    resize() {
      const width = Device.fov.height * 0.75;
      this.position.x = Device.fov.width * 0.5 + width + 2;
    }
  }
  