import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer.js";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass.js";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass.js";
// import { UnrealBloomPass } from 'three/addons/postprocessing/UnrealBloomPass.js';
import { CopyShader } from 'three/addons/shaders/CopyShader.js';
// import { GammaCorrectionShader } from 'three/addons/shaders/GammaCorrectionShader.js';
import { OutputPass } from 'three/addons/postprocessing/OutputPass.js';
import {
  Uniform,
  ShaderMaterial,
  Layers,
  // WebGLRenderTarget,
  // LinearFilter,
  // RGBAFormat,
  // AdditiveBlending,
  Color
} from "three";

import Common from "@/graphics/Common";
import Device from "@/pure/Device";
import { Vector2 } from "three";

import vertex from "./glsl/vertex.glsl";
import fragment from "./glsl/fragment.glsl";
import anamorphicVertex from "./glsl/anamorphic.vertex.glsl";
import anamorphicFragment from "./glsl/anamorphic.fragment.glsl";
import Assets from "@/graphics/Assets";

// const materials = [];
// const darkMaterial = new MeshBasicMaterial( { color: 'black' } );

const BLOOM_SCENE = 1;

const bloomLayer = new Layers();
bloomLayer.set( BLOOM_SCENE );
export default class {
  PARAMS = {
    stable: 1,
    colorShift: 0.01,
    effectFactor: 0.05,
    stableAdd: 0,
    stableAdd2: 0 
  }

  constructor() {
    this.init();
  }

  init() {
    const { PARAMS } = this;
    const { width, height } = Device.viewport;
    // var parameters = { minFilter: LinearFilter, magFilter: LinearFilter, format: RGBAFormat, stencilBuffer: false };
    // var renderTarget = new WebGLRenderTarget( Device.viewport.width, Device.viewport.height, parameters );
    // renderTarget.setPixelRatio(Device.pixelRatio);

    this.composer = new EffectComposer(Common.renderer);
    // this.composer.renderToScreen = false;

    this.anamorphicComposer = new EffectComposer( Common.renderer );
    this.anamorphicComposer.addPass(new ShaderPass(CopyShader));
    this.anamorphicComposer.setSize(Device.viewport.width * 0.1, Device.viewport.height * 0.1);
    this.anamorphicComposer.addPass( new RenderPass(Common.scene, Common.camera) );
    this.anamorphicComposer.renderToScreen = false;
    this.anamorphicComposerPass = new ShaderPass(
      new ShaderMaterial( {
        vertexShader: anamorphicVertex,
        fragmentShader: anamorphicFragment,
        // blending: AdditiveBlending,
        uniforms: {
          tDiffuse: { value: null },
          _invSize: {
            value: new Vector2(1 / Device.viewport.width, 1 / Device.viewport.height)
          }
        }
      } ), 'tDiffuse'
    );
    // this.anamorphicComposerPass.needsSwap = true;
    this.anamorphicComposer.addPass(this.anamorphicComposerPass);

    // const bloomPass = new UnrealBloomPass( new Vector2( Device.viewport.width, Device.viewport.height ), 0.0, 0.4, 0.85 );
    // bloomPass.threshold = 0.0;
    // bloomPass.strength = 0.5;
    // bloomPass.radius = 0.1
    // this.anamorphicComposer.addPass(bloomPass);
    // // this.composer.addPass( bloomPass );
    // this.bloomComposer = new EffectComposer( Common.renderer );
    // this.bloomComposer.renderToScreen = false;
    // this.bloomComposer.addPass( new RenderPass(Common.scene, Common.camera) );
    // this.bloomComposer.addPass( bloomPass );
    // this.bloomComposer.renderTarget2.texture.colorSpace = SRGBColorSpace
    this.mainPass = new ShaderPass(
      new ShaderMaterial( {
        vertexShader: vertex,
        fragmentShader: fragment,
        uniforms: {
          background: {
            value: new Color(0x030c1e).convertLinearToSRGB()
          },
          tDiffuse: { value: null },
    // this.scene.background = new Color(0x030c1e);
          aspectDiffuse: {
            value: 450 / 300
          },
          disperion: { value: Assets.glitch },
          // tDiffuse2: { value: this.composer.renderTarget2.texture },
          anamorphicTexture: { value: this.anamorphicComposer.renderTarget2.texture },
          // bloomTexture: { value: this.bloomComposer.renderTarget2.texture },
          resolution: {
            value: new Vector2(
              width * Device.pixelRatio,
              height * Device.pixelRatio,
            ),
          },
          stable: {
            value: PARAMS.stable
          },
          effectFactor: {
            value: PARAMS.effectFactor
          },
          time: new Uniform(0),
          colorShift: {
            value: PARAMS.colorShift
          }
        }
      } ), 'tDiffuse'
    );
    this.mainPass.needsSwap = true;
    this.outputPass = new OutputPass();
    // this.gammaCorrectionPass = new ShaderPass( GammaCorrectionShader );

    this.composer.addPass(new RenderPass(Common.scene, Common.camera));

    // this.renderer.out = LinearSRGBColorSpace;
    // this.handle.appendChild( this.renderer.domElement );
    // this.composer.addPass(this.gammaCorrectionPass);
    this.composer.addPass(this.mainPass);
    
    // this.composer.addPass(this.gammaCorrectionPass);
    // this.composer.addPass(this.mainPass);
    // this.composer.addPass(this.outputPass);
    // this.resize();
  }
  
  // darkenNonBloomed(obj) {
  //   if ( obj.isMesh && bloomLayer.test( obj.layers ) === false ) {
  //     materials[ obj.uuid ] = obj.material;
  //     obj.material = darkMaterial;
  //   }
  // }

  // restoreMaterial(obj) {
  //   if ( materials[ obj.uuid ] ) {

  //     obj.material = materials[ obj.uuid ];
  //     delete materials[ obj.uuid ];

  //   }
  // }

  dispose() {
    this.composer.reset();
  }

  render(t) {
    this.mainPass.uniforms.time.value = t;
    this.mainPass.uniforms.stable.value = this.PARAMS.stable + this.PARAMS.stableAdd + this.PARAMS.stableAdd2;
    
    // Common.scene.traverse( this.darkenNonBloomed );
    // this.bloomComposer.render();
    // Common.scene.traverse( this.restoreMaterial );

    // render the entire scene, then render bloom scene on top
    // this.composer.renderTarget2.texture.colorSpace = SRGBColorSpace;
    this.anamorphicComposer.render();
    this.composer.render();
    // this.mainPass.material.uniforms.tDiffuse.value.colorSpace = LinearSRGBColorSpace;

    // Common.render.toneMapping = NoToneMapping;

    // this.composer.renderTarget2.texture.colorSpace = LinearSRGBColorSpace;
    // this.renderer.render(Common.scene, Common.camera);
    // this.mainComposer.render();

    // Common.render.toneMapping = ACESFilmicToneMapping;
  }

  resize() {
    // this.bloomComposer.setSize(Device.viewport.width, Device.viewport.height);
    this.anamorphicComposer.setSize(Device.viewport.width * 0.1, Device.viewport.height * 0.1);
    this.composer.setSize(Device.viewport.width, Device.viewport.height);
    this.mainPass.uniforms.resolution.value.set(
      Device.viewport.width,
      Device.viewport.height,
    );
  }

  setDebug(pane) {
    const { PARAMS } = this;

    const folder = pane.addFolder({
      title: 'Postprocessing',
      expanded: false
    });
    folder
      .addBinding(
      PARAMS,
      'stable',
      {
          label: 'stable',
          step: 0.01,
          min: 0,
          max: 1,
      }
      )
      .on('change', () => {
        this.mainPass.material.uniforms.stable.value = PARAMS.stable
      });
    folder
      .addBinding(
      PARAMS,
      'colorShift',
      {
          label: 'colorShift',
          step: 0.001,
          min: -0.1,
          max: 0.1,
      }
      )
      .on('change', () => {
        this.mainPass.material.uniforms.colorShift.value = PARAMS.colorShift
      });
    folder
      .addBinding(
      PARAMS,
      'effectFactor',
      {
          label: 'effectFactor',
          step: 0.01,
          min: 0.0,
          max: 1,
      }
      )
      .on('change', () => {
        this.mainPass.material.uniforms.effectFactor.value = PARAMS.effectFactor
      });

  }
}
