<template>
    <div class="install" ref="el">
        <OSLanding ref="landing" />
        <div class="gabarit">
            <div class="heading bold">INCOMING TRANSMISSION</div>
            <div class="heading">RELIC . OS /> REQUIRE RETRIEVAL</div>
            <h2>
                <div class="row-1">
                    <span class="status">[ INITIALIZE ]</span> <span class="description">ENGRAM 15DK37555</span>
                </div>
                <div class="row-2">
                    <span class="status error">[ FAILED ]</span>
                </div>
            </h2>
        </div>
    </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue';
import gsap from "@gsap/shockingly";
import SplitText from "@gsap/shockingly/SplitText";
import OSLanding from './OSLanding.vue';
export default {
    name: "InstallComponent",
    components: { OSLanding },
    setup() {
        const el = ref(null);
        const landing = ref(null);
        const blink = () => {
            return gsap.timeline({
                repeat: 2
            })
                .set(el.value.querySelector(".row-1"), {
                    alpha: 0,
                    delay: 0.06
                })
                .set(el.value.querySelector(".row-1"), {
                    alpha: 1,
                    delay: 0.06
                })
        }
        const appear = () => {
            const tl = gsap.timeline();

            var head1 = new SplitText(el.value.querySelector(".heading.bold"), {type: "chars"});
            var head2 = new SplitText(el.value.querySelector(".heading:not(.bold)"), {type: "chars"});
            var head3 = new SplitText(el.value.querySelector(".description"), {type: "chars"});

            tl
                // .from(el.value.querySelector(".gabarit"), {
                //     alpha: 0
                // }, 'a')
                .from(head1.chars, {
                    alpha: 0,
                    stagger: 0.05
                }, 'a')
                .from(head2.chars, {
                    alpha: 0,
                    stagger: 0.05
                }, 'a+=0.5')
                .from(el.value.querySelector(".status"), {
                    alpha: 0,
                    x: 20
                }, 'a+=1.2')
                .from(head3.chars, {
                    alpha: 0,
                    stagger: 0.05
                }, 'b-=0.5')
                .add(blink())
                .to(el.value.querySelector(".row-1"), {
                    alpha: 0,
                }, 'c')
                .from(el.value.querySelector(".row-2"), {
                    alpha: 0,
                    x: 20,
                }, 'c')
            return tl;

        }
        onMounted(() => {
            const tl = gsap.timeline();
            tl.add(landing.value.enter(), 'a');
            tl
                .from(el.value.querySelector(".gabarit"), {
                    x: 180,
                    duration: 1.2,
                    ease: "power4.inOut"
                }, 'b-=1.2')
                .add(appear(), 'b-=1.2')
        });
        onUnmounted(() => {
            console.log("unmounted");
        });
        return {
            el,
            landing
        }
    }
}
</script>

