    text-shadow: 0 0 20px #77cc71;
}
h2 {
    font-weight: 500;
    font-size: 16px;
    margin-top: 4px;
}
.row-1 {
    position: absolute;
    white-space: nowrap;
}
.status {
    display: inline-flex;
}
