<template>
    <div class="os-box" ref="el">
        <div class="os">
            <div class="line top-1 line-1"></div>
            <div class="line top-2 line-2"></div>
            <div class="line bot-1 line-1"></div>
            <div class="line bot-2 line-2"></div>
            <div class="circle"></div>
            <div class="box">
                <span class="before"></span>
                <span class="label">RELIC</span>
            </div>
        </div>
        <div class="decoration">
            <div class="vertical-1">
                <span class="before"></span>
                <span class="after"></span>
            </div>
            <div class="center"></div>
            <div class="vertical-2">
                <span class="before"></span>
                <span class="after"></span>
            </div>
        </div>
    </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue';
import gsap from "@gsap/shockingly";
export default {
    name: "OSLanding",
    setup() {
        const el = ref(null);
        const enter = () => {
            const tl = gsap.timeline({
                delay: 0.5
            });
            tl
                .from(el.value.querySelector(".box .before"), {
                    scaleX: 0,
                    duration: 1,
                    ease: "quint.inOut"
                },'a')
            tl
                .to(el.value.querySelector(".center"), {
                    scaleY: 1,
                    duration: .75,
                    ease: "power4.in"
                },'a')
            tl
                .to(el.value.querySelectorAll("[class*=vertical] .before"), {
                    scaleY: 1,
                    duration: .75,
                    ease: "power4.out"
                },'a+=.75')
            tl
                .to(el.value.querySelector(".os"), {
                    x: 60,
                    duration: 1.2,
                    ease: "power4.inOut"
                }, 'a+=0.3')
            tl
                .to(el.value.querySelectorAll(".line-1"), {
                    scaleX: 1,
                    duration: 0.8,
                    ease: "sine.inOut"
                }, 'a+=0.3')
                .from(el.value.querySelectorAll(".line-1"), {
                    x: 40,
                    duration: 0.8,
                    ease: "sine.inOut"
                }, 'a+=0.3')
                .to(el.value.querySelectorAll(".line-2"), {
                    scaleX: 1,
                    duration: 0.8,
                    ease: "sine.inOut"
                }, 'a+=0.7')
                .from(el.value.querySelectorAll(".line-2"), {
                    x: -20,
                    duration: 0.8,
                    ease: "power4.out"
                }, 'a+=0.7')
            tl
                .from(el.value.querySelector(".circle"), {
                    alpha: 0,
                    duration: 1.2,
                    ease: "power4.inOut"
                }, 'a+=0.3')
            tl
                .from(el.value.querySelector(".circle"), {
                    x: -10,
                    duration: 0.6,
                    ease: "power4.out"
                }, 'a+=0.9')
            
            tl
                .from(el.value.querySelectorAll("[class*='vertical'] .after"), {
                    y: -40,
                    duration: 0.75,
                    ease: "quint.inOut"
                }, 'a+=1')
                .from(el.value.querySelectorAll("[class*='vertical'] .after"), {
                    alpha: 0,
                    duration: .2,
                    ease: "linear"
                }, 'a+=1')
                .add(
                    gsap.timeline({
                        repeat: 2
                    })
                        .set(el.value.querySelectorAll("[class*='vertical'] .after"), {
                            alpha: 0,
                            delay: 0.06
                        })
                        .set(el.value.querySelectorAll("[class*='vertical'] .after"), {
                            alpha: 1,
                            delay: 0.06
                        })
                    , 'a+=1.5'
                )
            tl
                .to(el.value.querySelector(".os"), {
                    x: -120,
                    duration: 1.2,
                    alpha: 0,
                    ease: "power4.inOut"
                }, 'b')
            tl
                .to(el.value.querySelector(".decoration"), {
                    x: -180,
                    duration: 1.2,
                    ease: "power4.inOut"
                }, 'b')
            tl
                .to(el.value.querySelector("[class*='line']"), {
                    scaleX: 0,
                    duration: 0.6,
                    ease: "power4.in"
                }, 'b')

            return tl;
        }
        onMounted(() => {
            // enter()
        });
        onUnmounted(() => {
            // console.log("unmounted");
        });
        return {
            el,
            enter
        }
    }
}
</script>

<style lang="scss" scoped>
