    color: #000000;
    // left: 60px;
    .box {
        padding: 0.5em 0.65em 0.5em 1.15em;
        position: relative;
        .before {
            position: absolute;
            background-color: #77cc71;
            box-shadow: 0 0 20px #77cc71;
            inset: 0;
            transform-origin: 0% 50%;
        }
    }
    .label {
        position: relative;
        z-index: 1;
        font-weight: 500;
    }
}
.line {
    height: 1px;
    width: 7px;
    position: absolute;
    overflow: hidden;
    opacity: .15;
    background-color: #77cc71;
    transform: scaleX(0);
    &[class*="top"] {
        bottom: 100%;
        margin-bottom: 45px;
        &[class$="-1"] {
            margin-bottom: 49px;
        }
    }
    &[class*="bot"] {
        top: 100%;
        margin-top: 45px;
        &[class$="-1"] {
            margin-top: 49px;
        }
    }
}
.circle {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    border: 1px solid #77cc71;
    position: absolute;
