    margin-left: 22px;
    top: 4px;
}
.decoration {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    .center {
        position: absolute;
        width: 1px;
        background-color: #77cc71;
        height: 64px;
        top: 50%;
        transform: translateY(-50%) scaleY(0);
    }
    [class*="vertical"] {
        position: absolute;
        width: 1px;
        // background-color: rgba(#77cc71, 0.3);
        height: 32px;
        bottom: 50%;
        margin-bottom: 38px;
        transform-origin: 50% 70px;
        .before {
            display: block;
            width: 100%;
            height: 100%;
            background-color: rgba(#77cc71, 0.15);
            transform: scaleY(0);
            transform-origin: 50% 100%;
        }

        .after {
            display: block;
            width: 3px;
            height: 3px;
            border-radius: 50%;
            background-color: #77cc71;
            position: absolute;
            bottom: 100%;
            margin-bottom: 6px;
            left: -1px;
        }
        
        &[class$="-2"] {
            transform: rotate(180deg);
        }
    }
}
</style>
  