import {
  LoadingManager,
  TextureLoader,
  // CubeTextureLoader,
  EquirectangularReflectionMapping,
  SRGBColorSpace,
  RepeatWrapping,
  RawShaderMaterial,
  Color,
  GLSL3,
  LinearSRGBColorSpace,
  NoColorSpace,
} from "three";

import { RGBELoader } from 'three/addons/loaders/RGBELoader.js';
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
// import { DRACOLoader } from 'three/addons/loaders/DRACOLoader.js';
// const dracoLoader = new DRACOLoader();
// dracoLoader.setDecoderPath( 'assets/draco/' );
// dracoLoader.setDecoderConfig( { type: 'js' } );

import LoveFont from '@/assets/fonts/Love.json';
import PPNeueMontrealMedium from '@/assets/fonts/PPNeueMontreal-Medium.json';

class Assets {
  manager = new LoadingManager(); 
  textureLoader = new TextureLoader(this.manager);
  rgbeLoader = new RGBELoader(this.manager);
  gltfLoader = new GLTFLoader(this.manager);
  constructor() {
    // this.gltfLoader.setDRACOLoader( dracoLoader );
  }
  init() {
    const { textureLoader, rgbeLoader } = this;

    this.LoveFont = LoveFont;
    this.PPNeueMontrealMedium = PPNeueMontrealMedium;

    this.alphaMap = textureLoader.load( "/assets/noise/transition4.png" );
    this.alphaMap.colorSpace = NoColorSpace;
    // this.alphaMap2 = textureLoader.load( "/assets/noise/cellular_round_ff_fractal.jpg" );
    // this.alphaMap2.colorSpace = NoColorSpace;
    this.glitch = textureLoader.load(`/assets/noise/glitche.jpeg`),
    this.glitch.colorSpace = NoColorSpace;
    
    this.map = textureLoader.load( "/assets/fonts/Love.png" );
    this.worldMap = textureLoader.load( "/assets/map.png" );
    this.worldMap.colorSpace = SRGBColorSpace;
    this.worldMap2 = textureLoader.load( "/assets/640px-Blue_Marble_2002.png" );
    this.worldMap2.wrapS = RepeatWrapping;
    this.worldMap2.colorSpace = SRGBColorSpace;
    this.worldMapClouds = textureLoader.load( "/assets/earthclouds.png" );
    this.worldMapClouds.wrapS = RepeatWrapping;
    this.worldMapClouds.colorSpace = SRGBColorSpace;
    this.ladyMargot = textureLoader.load( "/assets/lady-margot.jpeg" );
    this.ladyMargot.colorSpace = SRGBColorSpace;
    this.pixel = textureLoader.load( "/assets/pixel.png" );
    this.astodynamics = textureLoader.load( "/assets/SF-skill-Astrodynamics_0.png" );
    this.astodynamics.colorSpace = SRGBColorSpace;
    this.scavenging = textureLoader.load( "/assets/Starfield-Scavenging.png" );
    this.scavenging.colorSpace = SRGBColorSpace;

    this.jocodusTextures = [];
    this.jocodusTextures.push(this.pixel);
    this.jocodusTextures.push(textureLoader.load('/assets/globe-Jodocus-Hondius/L55008745_4.png'));
    this.jocodusTextures.push(textureLoader.load('/assets/globe-Jodocus-Hondius/L55008745_1.png'));
    this.jocodusTextures.push(textureLoader.load('/assets/globe-Jodocus-Hondius/L55008745_3.png'));
    this.jocodusTextures.push(textureLoader.load('/assets/globe-Jodocus-Hondius/L55008745_2.png'));
    this.jocodusTextures.push(this.pixel);
    this.jocodusTextures.forEach(_ => {
      if (_) _.colorSpace = SRGBColorSpace;
      if (_) _.flipY = false;
    })
    this.gltfLoader.load(
      '/assets/globe-Jodocus-Hondius/L55008745.glb',
      ( object ) => {
        this.jocodusGlobe = object.scene;
      },
    );
    this.gltfLoader.load(
      '/assets/models/dummy2.glb',
      ( object ) => {
        this.dummy = object;
      },
    );

    this.textureCube = rgbeLoader
      .setPath( '/assets/' )
      .load( 'potsdamer_platz_1k_11zon.hdr',  ( texture ) => {
        texture.mapping = EquirectangularReflectionMapping;
        texture.colorSpace = LinearSRGBColorSpace
      });



    this.glyphMaterial = new RawShaderMaterial({
      glslVersion: GLSL3,
        transparent: true,
      vertexShader: /* glsl */`
            precision highp float;
        in vec2 uv;
        in vec2 guv;
        in vec4 position;
        uniform mat4 projectionMatrix;
        uniform mat4 modelViewMatrix;
        out vec2 vUv;
        out vec2 vGuv;

        void main() {
          vUv = uv;
          vGuv = guv;

          vec3 transformed = vec3( position );

          vec4 mvPosition = vec4(transformed, 1.0);
          mvPosition = modelViewMatrix * mvPosition;

          vec4 pos = projectionMatrix * mvPosition;
          
          gl_Position = pos;
        }
      `,
      fragmentShader: /* glsl */`
        precision highp float;
        uniform vec3 color;
        uniform sampler2D map;
        in vec2 vUv;
        in vec2 vGuv;
        out vec4 myOutputColor;

        float median(float r, float g, float b) {
          return max(min(r, g), min(max(r, g), b));
        }	

        void main() {
          vec3 diffuseColor = color;
          float alpha = 1.0;

          vec3 msd = texture(map, vGuv).rgb;

          float sd = median(msd.r, msd.g, msd.b) - 0.5;
          alpha *= clamp(sd/fwidth(sd) + 0.5, 0.0, 1.0);

          myOutputColor = vec4(diffuseColor, alpha);
        }
      `,
        uniforms: {
            map: {
                value: this.map
            },
            color: {
                value: new Color(0xece9e3)
            }
        }
    });
  }
}

export default new Assets();
