<script>
import { inject, ref, onMounted, onUnmounted } from 'vue';
import gsap from '@gsap/shockingly';
import ScrollTrigger from "@gsap/shockingly/ScrollTrigger";
import Draggable from "@gsap/shockingly/Draggable";
import sound from "@/pure/Sound";
// import Input from '@/graphics/Input';
// import Device from '@/pure/Device';
import Lenis from 'lenis'

import Install from "@/components/Install/index.vue";

const lenis = new Lenis({
  orientation: 'horizontal',
  gestureOrientation: 'both'
})

// lenis.on('scroll', (e) => {
//   console.log(e)
// })
lenis.on('scroll', ScrollTrigger.update)
gsap.ticker.lagSmoothing(0)

export default {
  components: {
    Install
  },
  setup() {
    let app = inject('graphics');
    const corps = ref(null);
    const hero = ref(null);

    const { rotationY } = app.output.components.atlas.variables;
    const variables = {
      rotationY,
      velocity: 0.0
    };

    const proxy = document.createElement("div");
    let drag = null;
    let startProgress = 0;
    const progressWrap = gsap.utils.wrap(0, 1);
    const dragDistancePerRotation = 3000;
    const spin = gsap.fromTo(variables, {
      rotationY: Math.TAU
    }, {
      paused: true,
      rotationY: 0,
      duration: 20,
      ease: "none",
      repeat: -1,
    });

    // let st = null;
    // let velocityTween = null;
    // const updateVelocity = (velocity) => {
    //   if (velocityTween) {
    //     velocityTween.kill();
    //   }
    //   velocityTween = gsap.timeline({
    //     onUpdate: () => {
    //       Input.velocity = variables.velocity;
    //     }
    //   })
    //   const f = Math.min(1.0, Math.abs(velocity) / Device.viewport.height);
    //   const v = f * -Math.sign(velocity);

    //   velocityTween.set(variables, {
    //     velocity: v,
    //     duration: 0.05,
    //     ease: 'sine.in'
    //   }, 'a')
    //   velocityTween.to(variables, {
    //     velocity: 0,
    //     duration: 0.2 * f,
    //     ease: 'expo.out'
    //   }, 'a+=0.05')
    // }
    const tick = (time)=>{
      lenis.raf(time * 1000);
      // console.log(Math.abs(lenis.velocity))
    }
    onMounted(() => {
      lenis.on('scroll', (e) => {
        app.output.updatePage(e.animatedScroll/e.dimensions.limit.x);
        // console.log(Math.abs(lenis.velocity) / 50);
        app.output.components.postprocessing.PARAMS.stableAdd2 = -Math.abs(lenis.velocity) / 50;
      })
      gsap.ticker.add(tick)
      // app.output.addBackground({ el: corps.value })

      // st = new ScrollTrigger({
      //   trigger: document.body.querySelector("#app"),
      //   scrub: true,
      //   start: "top top-=1px",
      //   end: "bottom bottom+=1px",
      //   onUpdate: (self) => {
      //     const { end, progress } = self;
      //     app.output.updateY({
      //       end, progress
      //     });
      //     updateVelocity(self.getVelocity());
      //   }
      // });

      spin.progress((Math.TAU - rotationY) / (Math.TAU));
      const updateRotation = () => {
        let p = startProgress + (drag[0].startX - drag[0].x) / dragDistancePerRotation;
        spin.progress(progressWrap(p));
        if (app.output.components.atlas.sphere) {
          app.output.components.atlas.sphere.rotation.y = variables.rotationY;
          app.output.components.atlas.sphere.updateMatrix();
          

          app.output.components.gallica.rotation.y = app.output.components.atlas.sphere.rotation.y;
          app.output.components.gallica.updateMatrix();
        }
      }
      drag = Draggable.create(proxy, {
        trigger: hero.value,
        type: "x",
        inertia: true,
        allowNativeTouchScrolling: false,
        cursor: "inherit",
        onPress() {
          gsap.killTweensOf(spin);
          spin.timeScale(0);
          startProgress = spin.progress();
        },
        onDrag: updateRotation,
        onThrowUpdate: updateRotation,
      });
    });
    onUnmounted(() => {
      gsap.ticker.remove(tick)
      // app.output.disposeComponent('background');
      // st.kill();

      drag[0].kill();
    });
    const zipzap = () => {
      sound.play("boom-geomorphism");
      // sound.play("hit-low-gravity-absorber");
      // sound.play("ping-contact");
    };

    // const handleClick = () => {
    //   const lerp = app.output.components.mountains.children[0].children[0].material.userData.transition.value;
    //   if (lerp === 1) {
    //     app.output.components.mountains.enter();
    //   } else {
    //     app.output.components.mountains.leave();
    //   }
    // }

    return {
      corps,
      hero,
      zipzap
      // handleClick
    }
  }
}
</script>

