import {
    Object3D,
    Vector2,
} from "three";

import {
    SkeletonHelper,
    AnimationMixer,
    Clock
} from "three";

import Common from "@/graphics/Common";
import Circle from "@/graphics/components/Circle";

const actionables = [
    'Idle',
    'Walking',
    'Waving',
    'CastSpell',
]

export default class Character extends Object3D {
    PARAMS = {
        action: 'Idle',
    };
    constructor(oject) {
        super();

        this.clock = new Clock();

        this.model = oject.scene;
        this.animations = oject.animations.filter(({name}) => {
            return actionables.indexOf(name) !== -1;
        });
        this.mixer = new AnimationMixer( this.model );

        this.model.scale.set(2.2, 2.2, 2.2);
        this.model.position.y = -1.9;
        this.add(this.model);
        this.actions = {};


        for ( let i = 0; i !== this.animations.length; ++ i ) {
            let clip = this.animations[ i ];
            const name = clip.name;
            if (actionables.indexOf(name) !== -1) {
                const action = this.mixer.clipAction( clip );
                this.actions[name] = action;
                action.play();
            
                action.setEffectiveWeight( this.PARAMS.action === name ? 1 : 0 );
                
            }
        }

        this.circle = new Circle({
            diameter: 7,
            scale: 1,
            circle: 0.0,
            opacity: 0.3,
            arc: new Vector2(1.0, 0.5)
        });
        this.add(this.circle);
    }
    render() {
        const delta = this.clock.getDelta();
        this.mixer.update( delta );
    }
    resize() {
        this.circle.resize();
    }
    dispose() {
        if (this.skeleton) Common.scene.remove( this.skeleton );
        if (this.circle) this.add(this.circle);
    }

    prepareCrossFade( startAction, endAction ) {

        this.startAction = startAction;
        this.endAction = endAction;
        
        // if ( this.startAction === this.actions.Idle ) {
            this.executeCrossFade();
        // } else {
        //     this.synchronizeCrossFade();
        // }

    }
    synchronizeCrossFade() {
        this.mixer.addEventListener( 'loop', onLoopFinished );
        const that = this;

        function onLoopFinished( event ) {
            console.log("onLoopFinished", event.action, that.startAction);
            if ( event.action === that.startAction ) {

                that.mixer.removeEventListener( 'loop', onLoopFinished );

                that.executeCrossFade( that.startAction, that.endAction, 0.35 );

            }

        }

    }
    executeCrossFade() {
        this.setWeight( this.endAction, 1 );
        this.endAction.time = 0;
        this.startAction.crossFadeTo( this.endAction, 0.35, true );
    }
    setWeight( action, weight ) {
        action.enabled = true;
        action.setEffectiveTimeScale( 1 );
        action.setEffectiveWeight( weight );
    }
    setDebug(pane) {
        const { PARAMS } = this;
        this.skeleton = new SkeletonHelper( this.model );
        Common.scene.add( this.skeleton );
        let previousAction = PARAMS.action;

        const folder = pane.addFolder({
          title: 'Character',
          expanded: false
        });
        const options = this.animations.map(action => {
            action.name
            return {
                text: action.name,
                value: action.name
            }
        })
        folder
            .addBinding(PARAMS, 'action', {
                label: "Actions",
                options
            })
            .on('change', () => {
                const startAction = this.actions[previousAction];
                const endAction = this.actions[PARAMS.action];
                
                this.prepareCrossFade(startAction, endAction);

                previousAction = PARAMS.action;
            });
    }
}