import {
    Group,
} from "three";

import LineText from "@/graphics/components/Panel/LineText.js";

export default class Panel extends Group {
    PARAMS = {
        fontScale: 0.009,
    };
    constructor({
        title,
        count,
        details
    }) {
        super();
        this.setHeader({
            title,
            count,
            details
        });
        this.resize();
    }
    setHeader({
        title,
        count,
        details
    }) {
        this.header = new LineText({
            label: title
        });
        this.add(this.header);

        this.footer = new Group();
        if (count) {
            const { label, type, value } = count;
            this.footer.add(new LineText({
                label,
                lineWidth: 0.03,
                count: {
                    type,
                    value,
                }
            }));
        }
        if (details) {
            this.details = new LineText(details);
            this.footer.add(this.details);
        }
        // const date = new Date();
        // this.time = new LineText({
        //     label: `${date.getUTCHours().toString().padStart(2, "0")}:${date.getUTCMinutes().toString().padStart(2, "0")} UT`,
        //     alignRight: true 
        // });
        // this.footer.add(this.time);
        this.add(this.footer);
    }
    render() {
    }
    resize() {
        this.header.position.y = 1;
        this.footer.position.y = -1 - 0.2;
        if (this.details) this.details.position.y = - 0.35;
    }
    dispose() {
        this.remove(this.header);
        this.remove(this.footer);
    }
}