import {
  Group,
} from "three";

import gsap from "@gsap/shockingly";

import Device from "@/pure/Device";

import Circle from "@/graphics/components/Circle";

export default class Ambiance extends Group {
  constructor() {
    super();
    this.matrixAutoUpdate = false;
    this.circle = new Circle({
      diameter: 10,
      scale: 1,
      circle: 0,
      opacity: 0.1,
    });
    this.add(this.circle);
  }
  enter() {

    const lerp = {
      x: this.circle.material.uniforms.arc.value.x,
      y: this.circle.material.uniforms.arc.value.y
    }
    this.tl = new gsap.timeline();
    this.tl.to(lerp, {
      x: 1,
      y: 1,
      duration: 5,
      ease: "sine.inOut",
      onUpdate: () => {
        this.circle.material.uniforms.arc.value.x = lerp.x
        // this.circle.material.uniforms.arc.value.y = lerp.y
      }
    }, 'a');

  }
  render() {
  }
  dispose() {
    this.circle.dispose();
  }
  resize() {
    this.circle.diameter = Device.fov.width;
    this.position.y = -Device.fov.height * 0.5;
    this.circle.resize();
    this.updateMatrix();
    
  }
}
